<template>
<div class="news-types" v-if="channels.length>=0">
    <div class="news-btn" 
    v-for='item in show' 
    :key='item.channelId'
    :class="{active:item.channelId===chooseId}"
    @click="SwitchId(item.channelId)">
        <span>{{item.name}}</span>
    </div>
    
    <a href="" @click.prevent='iscoll=!iscoll'>{{iscoll?'展开':'收起'}}</a>
</div>
 
</template>

<script>

import { getNewsChannels } from "../services/newservices"
export default {
    
    data(){
        return{
            channels:[],
            iscoll:true,
            chooseId:null,
        }
    },
    methods:{
        SwitchId(id){
            this.chooseId=id;
            this.$emit('Choose',this.chooseId)
        }
    },
    async created(){
      var res=await getNewsChannels();
      this.channels=res;

      this.SwitchId(this.channels[0].channelId);
      console.log(this.channels[0].channelId);
    },
     computed:{
        show:function(){
            if(this.iscoll){
                return this.channels.slice(0,8);
                
            }else{
               
                return this.channels;
            }
        }
    },
}
</script>

<style scoped>
    .news-types{
        width: 100%;
        margin-top: 150px;
        background-color: #fff;
        margin: 0 30px;
        border-bottom: 1px solid #ccc;
        display: flex;
        flex-wrap: wrap;
        font-size: 15px;
        padding-bottom: 15px;
    }
    .news-types a{
        font-size: 14px;
        margin-top: 30px;
        color: deepskyblue;
    }
    .news-btn{
        height: 30px; 
        line-height: 30px;
        padding: 10px 20px;
        margin-right: 30px;
        margin-top: 15px;
        border-radius: 5px;
        border: 1px solid #ccc;
        background-color: #fff;
    }

    
     .news-btn span{
         width: 100%;
        
     }
     .news-btn:hover{
         background-color: #ccc;
     }
     .news-btn.active{
         background-color: #000;
         color: #fff;
     }

    

</style>


