<template>
  <div class="wrap">
        <ul class="list">
            <li class="item active">1</li>
            <li class="item">2</li>
            <li class="item">3</li>
            <li class="item">4</li>
            <li class="item">5</li>
            <li class="item">6</li>
            <li class="item">7</li>
        </ul>
        <button class="btn" id="left">{{left}}</button> <button class="btn" id="right">{{right}}
        </button>
        <ul class="wrap-btn">
            <li class="btn-item"><button class="weap-icon" data-index='0'></button></li>
            <li class="btn-item"><button class="weap-icon" data-index='1'></button></li>
            <li class="btn-item"><button class="weap-icon" data-index='2'></button></li>
            <li class="btn-item"><button class="weap-icon" data-index='3'></button></li>
            <li class="btn-item"><button class="weap-icon" data-index='4'></button></li>
            <li class="btn-item"><button class="weap-icon" data-index='5'></button></li>
            <li class="btn-item"><button class="weap-icon" data-index='6'></button></li>
               
        </ul>
   
    </div>

</template>

<script>
import '../assets/js/main'

export default {
 data(){
     return{
         left:"<",
         right:'>'
     }
 }
}
</script>

<style scpoed>
.wrap{
    position: relative;
    width: 100%;
    height: 300px;

}
.list{
    width: 100%;
    height: 300px;
    list-style: none;
    position: relative;
}
.item{
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    transition: all 0.8s ease;
}
.item:nth-child(1){
    background: url(../llilis/banner01.png) no-repeat center center/cover;
}
.item:nth-child(2){
    background: url(../llilis/banner02.png) no-repeat center center/cover;
}
.item:nth-child(3){
    background: url(../llilis/banner03.png) no-repeat center center/cover;
}
.item:nth-child(4){
    background: url(../llilis/banner04.png) no-repeat center center/cover;
}
.item:nth-child(5){
    background: url(../llilis/banner05.png) no-repeat center center/cover;
}
.item:nth-child(6){
    background: url(../llilis/banner06.png) no-repeat center center/cover;
}
.item:nth-child(7){
    background: url(../llilis/banner07.png) no-repeat center center/cover;
}
.item.active{
    opacity: 1;
    z-index: 40;
}
.btn{
    position: absolute;
    width: 50px;
    height: 100px;
    outline: none;
    background-color: rgba(0,0,0,0.7);
    color: #fff;
    border: none;
    z-index: 50;
}
.btn:hover{
    background-color: rgba(0,0,0,0.85);
    
}
#left{
    left: 0;
    top: 100px;
}
#right{
    right: 0;
    top: 100px;
}
.wrap-btn{
    
    width: 250px;
    height: 50px;

    position: absolute;
    bottom: 0;
    right: 0;
    margin-right: 15px;
    z-index: 120;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.wrap-btn .btn-item button{
    width: 15px;
    height: 15px;
    outline: none;
    background-color: #999;
    border: 3px solid #666666; 
    border-radius: 50%;

}
.wrap-btn .btn-item button:hover{
    background-color: #777;
    border-color: #888;
}
 
</style>


