<template>
  <div id="home">
   
    
    <div class="banner">
      <div class="container">
        <Banner></Banner>
      </div>
    </div>

    <div class="news-ch">
      <div class="container">
        <Channels @Choose='hello'></Channels>
      </div>
    </div>

<!-- <Center>
      <LoadingNews v-show="isLoading"></LoadingNews>
   </Center> -->
  <div class="news-list">
        <div class="container">
          <NewsList  :news='news'></NewsList>
        </div>
      </div>
   
  
      
    
    
     
    
  </div>
</template>

<script>
import Banner from './banner'
import Channels from './channels'
import NewsList from './newslist'
import { getnews } from '../services/newservices'
import LoadingNews from './loading'
import Center from './center';
export default {
  data(){
    return{
       news:[],
      
    }
   
  },
  components:{
    Banner,
    Channels,
    NewsList,
    LoadingNews,
    Center
  },
  methods:{
      async hello(newid){
     
       var res=await getnews(newid,1,20); 
       this.news=res.contentlist;
       return this.newid

    }
  }
}
</script>

<style  scoped>
  #home{
    width: 100%;
    overflow: hidden;
   
  }
</style>



